.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.cs-faq .toggle-list {
  li {
    border-bottom: .266vw #f1f1f1 solid;
    .ques {
      padding: 3.333vw 4.8vw;
      line-height: 5.333vw;
      font-size: 3.733vw;
      color: #898989;
      position: relative;
      a {
        display: block;
        &::after {
          content: '';
          display: inline-block;
          color: #ccc;
          position: absolute;
          width: 2.533vw;
          height: 1.466vw;
          right: 4.8vw;
          top: 50%;
          background: url('/_ui/img/m/icn/icn-arrow07.png') no-repeat;
          background-size: contain;
          transform: translateY(-50%);
        }
        span {
          display: block;
          width: 85.333vw;
        }
      }
      a.on {
        &::after {
          -webkit-transform: rotate(180deg);
          -moz-transform: rotate(180deg);
          -o-transform: rotate(180deg);
          -ms-transform: rotate(180deg);
          transform: rotate(180deg);
        }
      }
    }
    .answer {
      display: none;
      padding: 6.666vw 4.8vw;
      background: #f8f8f8;
      font-size: 3.733vw;
      color: #898989;
      line-height: 6.666vw;
      overflow: hidden;
      span {
        display: block;
      }
      .close {
        float: right;
        margin-top: 5.333vw;
      }
    }
    &:last-child {
      border-bottom: 0;
    }
  }
}
.toggle-list.notice {
  padding: 10.133vw 0 0;
  min-height: 300px;
  .ques a span {
    color: #898989;
    word-break: keep-all;
    word-wrap: break-word;
  }
  img {
    max-width: 100%;
  }
}
.notice-more {
  font-size: 0;
  text-indent: -999px;
  display: block;
  width: 7.733vw;
  height: 7.733vw;
  background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
  margin: 11.733vw auto 16.533vw;
  background-size: 100%;
}

@media (min-width: 750px) {
  .scr-navi {
    width: 678px;
    .swiper-wrapper {
      .swiper-slide a {
        height: 100px;
        line-height: 100px;
        font-size: 30px;
      }
    }
    .swiper-wrapper + .icn-navi {
      width: 35px;
      height: 115px;
      right: -36px;
    }
    .swiper-wrapper + .icn-navi.left {
      left: -36px;
    }
  }
  .toggle-list.notice {
    padding: 76px 0 0;
  }
  .notice-more {
    width: 58px;
    height: 58px;
    margin: 88px auto 124px;
  }
  .cs-faq .toggle-list li {
    border-bottom: 2px #f1f1f1 solid;
    .ques {
      padding: 25px 36px;
      line-height: 40px;
      font-size: 28px;
      a {
        &::after {
          width: 19px;
          height: 11px;
          right: 36px;
        }
        span {
          width: 640px;
        }
      }
    }
    .answer {
      padding: 50px 36px;
      font-size: 28px;
      line-height: 50px;
      .close {
        margin-top: 40px;
      }
    }
  }
}
